  .sign {
    padding-top: 0.3rem; } }


@media only screen and (min-height: 320px) {
  .sign {
    padding-top: 1.3rem; } }

@media only screen and (min-height: 480px) {
  .sign {
    padding-top: 3rem; } }

@media only screen and (min-height: 568px) {
